import React, { createContext, useContext, useState, useEffect } from 'react';
import {
  getFirestore,
  collection,
  doc,
  getDocs,
  getDoc,
  updateDoc,
  arrayUnion,
} from 'firebase/firestore';
import { useAuth } from './AuthContext';

const LearningContext = createContext();

export const useLearning = () => useContext(LearningContext);

export const LearningProvider = ({ children }) => {
  const { user } = useAuth();
  const [courses, setCourses] = useState([]);
  const [selectedCourse, setSelectedCourse] = useState(null);
  const [nextLesson, setNextLesson] = useState(null);
  const [nextWorkbook, setNextWorkbook] = useState(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);

  const db = getFirestore();

  useEffect(() => {
    const fetchCourses = async () => {
      if (!user) {
        setLoading(false);
        return;
      }

      try {
        setLoading(true);

        // Fetch all courses
        const coursesSnapshot = await getDocs(collection(db, 'courses'));
        const coursesData = coursesSnapshot.docs.map((doc) => ({
          id: doc.id,
          ...doc.data(),
        }));
        setCourses(coursesData);

        // Fetch user data
        const userDocRef = doc(db, 'users', user.uid);
        const userDoc = await getDoc(userDocRef);
        const userData = userDoc.exists() ? userDoc.data() : {};

        const finishedLessons = userData.finishedLessons || [];
        const finishedWorkbooks = userData.finishedWorkbooks || [];

        // Fetch selected course data
        if (user.courseID) {
          const courseDocRef = doc(db, 'courses', user.courseID);
          const courseDoc = await getDoc(courseDocRef);

          if (courseDoc.exists()) {
            const courseData = { id: courseDoc.id, ...courseDoc.data() };

            // Fetch modules
            const modulesSnapshot = await getDocs(collection(courseDocRef, 'modules'));
            const modulesData = await Promise.all(
              modulesSnapshot.docs.map(async (moduleDoc) => {
                const moduleData = { id: moduleDoc.id, ...moduleDoc.data() };

                // Fetch lessons for each module
                const lessonsSnapshot = await getDocs(
                  collection(courseDocRef, 'modules', moduleDoc.id, 'lessons')
                );
                const lessonsData = lessonsSnapshot.docs.map((lessonDoc) => ({
                  id: lessonDoc.id,
                  moduleID: moduleDoc.id,
                  ...lessonDoc.data(),
                }));

                // Fetch workbooks for each module
                const workbooksSnapshot = await getDocs(
                  collection(courseDocRef, 'modules', moduleDoc.id, 'workbooks')
                );
                const workbooksData = workbooksSnapshot.docs.map((workbookDoc) => ({
                  id: workbookDoc.id,
                  moduleID: moduleDoc.id,
                  ...workbookDoc.data(),
                }));

                return {
                  ...moduleData,
                  lessons: lessonsData,
                  workbooks: workbooksData,
                };
              })
            );

            // Flatten lessons and workbooks
            const allLessons = modulesData.flatMap((module) => module.lessons);
            const allWorkbooks = modulesData.flatMap((module) => module.workbooks);

            // Determine next lesson and workbook
            const unfinishedLessons = allLessons.filter(
              (lesson) => !finishedLessons.some((finished) => finished.lessonID === lesson.id)
            );
            const unfinishedWorkbooks = allWorkbooks.filter(
              (workbook) => !finishedWorkbooks.some((finished) => finished.workbookID === workbook.id)
            );

            setNextLesson(unfinishedLessons[0] || null);
            setNextWorkbook(unfinishedWorkbooks[0] || null);

            setSelectedCourse({
              ...courseData,
              modules: modulesData,
              lessons: allLessons,
              workbooks: allWorkbooks,
              finishedLessons,
              finishedWorkbooks,
            });
          } else {
            setError('Selected course does not exist.');
          }
        }
      } catch (err) {
        console.error('Error fetching course data:', err);
        setError('Failed to fetch course data.');
      } finally {
        setLoading(false);
      }
    };

    fetchCourses();
  }, [db, user]);

  const finishLesson = async (lessonID, lessonStats = {}) => {
    try {
      const userRef = doc(db, 'users', user.uid);
      const newFinishedLesson = {
        lessonID,
        ...lessonStats,
        date: new Date().toISOString(),
      };

      await updateDoc(userRef, {
        finishedLessons: arrayUnion(newFinishedLesson),
        currentLessonID: lessonID,
      });

      // Update local state
      setSelectedCourse((prevCourse) => {
        if (!prevCourse) return prevCourse;

        const updatedFinishedLessons = [...prevCourse.finishedLessons, newFinishedLesson];

        const unfinishedLessons = prevCourse.lessons.filter(
          (lesson) => !updatedFinishedLessons.some((finished) => finished.lessonID === lesson.id)
        );

        return {
          ...prevCourse,
          finishedLessons: updatedFinishedLessons,
        };
      });

      setNextLesson((prevCourse) => {
        if (!prevCourse) return null;

        const unfinishedLessons = prevCourse.lessons.filter(
          (lesson) =>
            !prevCourse.finishedLessons.some((finished) => finished.lessonID === lesson.id)
        );

        return unfinishedLessons[0] || null;
      });
    } catch (error) {
      console.error('Error finishing lesson:', error);
      setError('Failed to finish the lesson.');
    }
  };

  const finishWorkbook = async (workbookID, workbookStats = {}) => {
    try {
      const userRef = doc(db, 'users', user.uid);
      const newFinishedWorkbook = {
        workbookID,
        ...workbookStats,
        date: new Date().toISOString(),
      };
  
      await updateDoc(userRef, {
        finishedWorkbooks: arrayUnion(newFinishedWorkbook),
        currentWorkbookID: workbookID,
      });
  
      // Update local state
      setSelectedCourse((prevCourse) => {
        if (!prevCourse) return prevCourse;
  
        // Ensure finishedWorkbooks is an array
        const updatedFinishedWorkbooks = [
          ...(prevCourse.finishedWorkbooks || []),
          newFinishedWorkbook,
        ];
  
        // Ensure workbooks is an array
        const unfinishedWorkbooks = (prevCourse.workbooks || []).filter(
          (workbook) =>
            !updatedFinishedWorkbooks.some(
              (finished) => finished.workbookID === workbook.id
            )
        );
  
        return {
          ...prevCourse,
          finishedWorkbooks: updatedFinishedWorkbooks,
          workbooks: prevCourse.workbooks, // No change to workbooks, just returning as it is
        };
      });
  
      console.log('Workbook finished successfully.');
    } catch (error) {
      console.error('Error finishing workbook:', error);
      throw new Error('Failed to finish the workbook.');
    }
  };
  

  return (
    <LearningContext.Provider
      value={{
        courses,
        selectedCourse,
        setSelectedCourse,
        nextLesson,
        nextWorkbook,
        finishLesson,
        finishWorkbook,
        loading,
        error,
      }}
    >
      {!loading && children}
    </LearningContext.Provider>
  );
};
